}
</style>
<script>
import mapboxgl from "mapbox-gl";

//marker;
 // map;
export default {


    data() {
        return {

        MyaccessToken:
        "pk.eyJ1IjoibWVycmlsbGdvbnNhbHZlcyIsImEiOiJjajN6ampkbzcwM3VxMzJyemR5Y2dqdTZuIn0.EGosEkXFJM5K_82Vy0XuCA",
   
          placesData: [
        {
          description: "Chennai",
          coordinates: [80.21117527116763, 13.008852534856274],
        },
        {
          description: "Dubai",
          coordinates: [55.29004174850411, 25.280927196903818],
        },
        {
          description: "America",
          coordinates: [-77.090372, 38.881189],
        },
      ],         

            
        }
    },
     methods: {
    setupMapBox() {
      mapboxgl.accessToken = this.MyaccessToken;
      this.map = new mapboxgl.Map({
        container: "map",

        //style: 'mapbox://styles/mapbox/streets-v9',
        style: "mapbox://styles/mapbox/light-v11",
        zoom: 1.6,
        projection: {
          name: "equirectangular",
          center: [80.16684476066258, 12.983596468763904],
        },
      });
    },
    markers(coordinates=[80.16684476066258, 12.983596468763904]) {
      this.marker1 = new mapboxgl.Marker({ "color": "#0072FF" })
        .setLngLat(coordinates)
        .addTo(this.map);
    },
    markerPopup() {
      this.popup = new mapboxgl.Popup({
        closeButton: false,
        closeOnClick: false,
      });
    },

    rowClick(row) {
      this.$router.push("airlinedetails/");
      console.log(row.name);
    },
  },

  mounted() {
    this.setupMapBox();
    this.placesData.forEach(markerData=>{
        this.markers(markerData.coordinates)
    })
    this.markers();
    this.markerPopup();

    this.map.on("load", () => {
      this.map.addSource("places", {
        type: "geojson",
        data: {
          type: "FeatureCollection",
          features: [
            {
              type: "Feature",
              properties: {
                description: "<strong>Chennai</strong>",
              },
              geometry: {
                type: "Point",
                coordinates: [80.21117527116763, 13.008852534856274],
              },
            },
            {
              type: "Feature",
              properties: {
                description: "<strong>Dubai</strong>",
              },
              geometry: {
                type: "Point",
                coordinates: [55.29004174850411, 25.280927196903818],
              },
            },
            {
              type: "Feature",
              properties: {
                description: "<strong>America</strong>",
              },
              geometry: {
                type: "Point",
                coordinates: [-77.090372, 38.881189],
              },
            },
          ],
        },
      });

      this.map.addLayer({
        id: "places",
        //type: "symbol",
        source: "places",
        'type': 'circle',
          /*'paint': {
            'circle-radius': 10,
            'circle-color': '#448ee4'
          }*/
      });

      this.map.on("mouseenter", "places", (e) => {
        // Change the cursor style as a UI indicator.
        this.map.getCanvas().style.cursor = "pointer";

        // Copy coordinates array.
        const coordinates = e.features[0].geometry.coordinates.slice();
        const description = e.features[0].properties.description;
        console.log(this.description);

        // Ensure that if the map is zoomed out such that multiple
        // copies of the feature are visible, the popup appears
        // over the copy being pointed to.
        while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
          coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
        }

        // Populate the popup and set its coordinates
        // based on the feature found.
        this.popup.setLngLat(coordinates).setHTML(description).addTo(this.map);
      });

      this.map.on("mouseleave", "places", () => {
        this.map.getCanvas().style.cursor = "";
        this.popup.remove();
      });
    });
